import React, { useState, useContext, useEffect } from 'react';
import { Link, NavLink, useNavigate } from "react-router-dom";
import cities from './CitiesList';
import $ from 'jquery';

import { UserContext } from "../App";

export default function Header() {
    let navigate = useNavigate();
    const {state, dispatch} = useContext(UserContext);
    let adm = localStorage.getItem("admin");
    useEffect(() => {
        let token = localStorage.getItem("token");
        if (token) {
            dispatch({type:"USER", payload:true});
        } else {
            dispatch({type:"USER", payload:false});
        }
    }, []);
    const logOut = () => {
        localStorage.removeItem("token");
        dispatch({type:"USER", payload:false});
        navigate("/");
    };
    $(document).click(function(e){
        if( !$(e.target).closest(".user-panel").length > 0 ) {
            $("#menu_wrap").removeClass("open");
        } if ( !$(e.target).closest(".header-nav, .menu_icon").length > 0 ) {
            $(".header-nav").removeClass("open");
        }
    });
    const menuToggle = () => {
        $("#menu_wrap").addClass("open");
    };
    const mobileMenu = () => {
        if($(".header-nav").hasClass("open")) {
            $(".header-nav").removeClass("open");
        } else {
            $(".header-nav").addClass("open");
        }
    };
    const url = window.location.pathname.split('/').pop();
    useEffect(() => {
        $(".header-nav").removeClass("open");
    }, [url]);
    const RenderMenu = () => {
        if(!state) {
            return (
                <>
                    <NavLink to="/login" exact="true" className="login">Login</NavLink>
                    <NavLink to="/register" exact="true" className="register">Join us</NavLink>
                </>
            )
        } else {
            return (
                <>
                    <a className="menu_item" onClick={menuToggle}>
                        <img src="/img/menu.png" alt="menu" />
                    </a>
                    <div className="logged_menu" id="menu_wrap">
                        <NavLink to="/account" className="submenu_items">Account</NavLink>
                        <NavLink to="/dashboard" className="submenu_items">Dashboard</NavLink>
                        <a onClick={logOut} className="submenu_items">Logout</a>
                    </div>
                </>
            )
        }
    }
    const curCity = localStorage.getItem("city");
    const [city, setCity] = useState(curCity);
    const [citystr, setCitystr] = useState('');
    const [cityshow, setCityshow] = useState(false);

    const citySelect = (e) => {
        localStorage.setItem("city", e.target.innerHTML);
        setCity(e.target.innerHTML);
        document.getElementById('city_input').value = '';
        setCityshow(false);
    }

    const cityString = (e) => {
        if(e.target.value.length > 2) {
            setCityshow(true);
        } else {
            setCityshow(false);
        }
        setCitystr(e.target.value);
    }

    const filterCity = cities.filter((element) => {
        return element.toLowerCase().match(citystr.toLowerCase());
    });

    return (
        <>
            <header className="header-section">
                <Link to="/" className="site-logo">
                    <img src="/img/logo.png" alt="logo" />
                </Link>
                <div className="current_city_wrap">
                    <div className="current_cities main-menu">
                        <span className="current_city_box">{curCity ? city : "Your City"}</span>
                        <div className="cities_submenu sub-menu">
                            <div className="all_cities_wrap">
                                <p className="city_head">Top Cities</p>
                                <div className="city_list">
                                    <span className="city_name" onClick={citySelect}>Delhi</span>
                                    <span className="city_name" onClick={citySelect}>Mumbai</span>
                                    <span className="city_name" onClick={citySelect}>Bangalore</span>
                                    <span className="city_name" onClick={citySelect}>Chennai</span>
                                    <span className="city_name" onClick={citySelect}>Kolkata</span>
                                </div>
                                <div className="city_search">
                                    <input type="text" className="city_input" id="city_input" autoComplete="off" onChange={cityString} placeholder="Search City" />
                                </div>
                                {(cityshow && filterCity.length !== 0) ? <div className="city_list_wrap">
                                    {filterCity.map((filtcities) => <span className="city_name" key={filtcities} onClick={citySelect}>{filtcities}</span>)}
                                </div> : ""}
                            </div>
                        </div>
                    </div>
                </div>
                <span className="menu_icon" onClick={mobileMenu}><img src="/img/menu.png" alt="menu icon" /></span>
                <nav className="header-nav">
                    <ul className="main-menu">
                        <li><NavLink to="/" exact="true">Home</NavLink></li>
                        <li><NavLink to="about" exact="true">About</NavLink></li>
                        <li><NavLink to="/contact" exact="true">Contact</NavLink></li>
                    </ul>
                    <div className="header-right">
                        {!adm?
                        <div className="user-panel">
                            <RenderMenu />
                        </div>:<NavLink to="/admindashboard" exact="true" className="register">Dashboard</NavLink>}
                    </div>
                </nav>
            </header>
        </>
    )
}