import React, { Suspense, lazy, createContext, useReducer } from 'react';
import './plugins/bootstrap/bootstrap.min.css';
import './plugins/fontawesome/css/all.min.css';
import './plugins/style.css';
import Header from "./components/Header";
import Footer from './components/Footer';
import PrivateRoute from './components/validation/PrivateRoute';
import AdminRoute from './components/validation/AdminRoute';
import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";

import { initialState, reducer } from './components/reducer/UseReducer';

const Home = lazy(() => import('./components/Home'));
const About = lazy(() => import('./components/About'));
const Contact = lazy(() => import('./components/Contact'));
const SearchResult = lazy(() => import('./components/SearchResult'));
const Property = lazy(() => import('./components/Property'));
const Login = lazy(() => import('./components/Login'));
const Register = lazy(() => import('./components/Register'));
const Terms = lazy(() => import('./components/Terms'));
const Testimonials = lazy(() => import('./components/Testimonials'));
const Account = lazy(() => import('./components/Account'));
const Dashboard = lazy(() => import('./components/Dashboard'));
const ShortlistedProp = lazy(() => import('./components/ShortlistedProp'));
const OfferedProp = lazy(() => import('./components/OfferedProp'));
const ComongSoon = lazy(() => import('./components/ComongSoon'));
const Notfound = lazy(() => import('./components/Notfound'));

const AdminLogin = lazy(() => import('./components/admin/AdminLogin'));
const Admindashboard = lazy(() => import('./components/admin/Admindashboard'));
const Admin = lazy(() => import('./components/admin/Admin'));
const Offered = lazy(() => import('./components/admin/Offered'));
const Inquiries = lazy(() => import('./components/admin/Inquiries'));
const Contacts = lazy(() => import('./components/admin/Contacts'));
const EditSearch = lazy(() => import('./components/admin/EditSearch'));
const EditProps = lazy(() => import('./components/admin/EditProps'));


export const UserContext = createContext();

const Routing = () => {
  return (
    <div className="body-inner">
      <Header />
      <Suspense fallback={<div id="preloder"><div className="loader"></div></div>}>
        <Routes>
          <Route exact path="/" element={<Home />} />
          <Route path="/about" element={<About />}/>
          <Route path="/contact" element={<Contact />}/>
          <Route path="/searchresult/:id" element={<SearchResult />}/>
          <Route path="/property/:id" element={<Property />}/>
          <Route path="/login" element={<Login />}/>
          <Route path="/register" element={<Register />}/>
          <Route path="/termsandconditions" element={<Terms />}/>
          <Route path="/testimonials" element={<Testimonials />}/>
          <Route path="/careers" element={<ComongSoon />}/>
          <Route path="/with-us" element={<ComongSoon />}/>
          <Route path="/papers" element={<ComongSoon />}/>
          <Route path="/clients" element={<ComongSoon />}/>
          <Route path="/guideline" element={<ComongSoon />}/>
          <Route path="/adminlogin" element={<AdminLogin />}/>
          <Route element={<PrivateRoute  />}>
            <Route path="/account" element={<Account />}/>
            <Route path="/dashboard" element={<Dashboard />}/>
            <Route path="/post-property" element={<ComongSoon />}/>
            <Route path="/shortlisted-property" element={<ShortlistedProp />}/>
            <Route path="/offerred-property" element={<OfferedProp />}/>
          </Route>
          <Route element={<AdminRoute  />}>
            <Route path="/admindashboard" element={<Admindashboard />}/>
            <Route path="/addproperty" element={<Admin />}/>
            <Route path="/offered" element={<Offered />}/>
            <Route path="/inquiries" element={<Inquiries />}/>
            <Route path="/contacts" element={<Contacts />}/>
            <Route path="/editproperty" element={<EditSearch />}/>
            <Route path="/editprops/:id" element={<EditProps />}/>
          </Route>
          <Route path="*" element={<Notfound />}/>
        </Routes>
      </Suspense>
      <Footer />
    </div>
  )
}

const App = () => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [adm, dispadm] = useReducer(reducer, initialState);
  return (
    <>
      <BrowserRouter>
        <UserContext.Provider value={{state, dispatch, adm, dispadm}}>
          <Routing />
        </UserContext.Provider>
      </BrowserRouter>
    </>
  );
}
export default App;